import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Loader from "react-loader-spinner";

import PrimaryButton from "../../elements/PrimaryButton";
import Alert from "elements/Alert";

// Actions
import { register } from "actions/user";

class RegisterPage extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      formData: {
        firstname: "",
        lastname: "",
        email: "",
        password: "",
        tnc: false,
      },
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleRegisterFormSubmit = this.handleRegisterFormSubmit.bind(this);
  }

  handleChange({ target }) {
    let { name, value } = target;

    value = target.type === "checkbox" ? target.checked : value;

    this.setState((prevState) => ({
      ...prevState,
      formData: { ...prevState.formData, [name]: value },
    }));
  }

  handleRegisterFormSubmit(e) {
    e.preventDefault();

    // console.log("register fomr data = ", this.state.formData);

    this.props.register(this.state.formData);
  }

  render() {
    return (
      <div className="w-full h-screen flex flex-col justify-center items-center bg-gray-300">
        <img
          className="w-16"
          src="/assets/images/botsite-logo.svg"
          alt="Botsite Logo"
        />
        <h2 className="text-4xl text-gray-700">Register Botsite</h2>
        <form
          className="w-independent-form bg-gray-300 rounded px-4 py-4 pt-8"
          onSubmit={this.handleRegisterFormSubmit}
        >
          {!!this.props.registerError && (
            <Alert type="error" heading={this.props.registerError} />
          )}

          <div className="pb-4">
            <label
              htmlFor="firstname"
              className="text-sm block text-gray-500 pb-2"
            >
              First Name
            </label>
            <input
              type="text"
              name="firstname"
              id="firstname"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
              placeholder="John"
              value={this.state.formData.firstname}
              onChange={this.handleChange}
            />
          </div>

          <div className="pb-4">
            <label
              htmlFor="lastname"
              className="text-sm block text-gray-500 pb-2"
            >
              Last Name
            </label>
            <input
              type="text"
              name="lastname"
              id="lastname"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
              placeholder="Wick"
              value={this.state.formData.lastname}
              onChange={this.handleChange}
            />
          </div>

          <div className="pb-4">
            <label htmlFor="email" className="text-sm block text-gray-500 pb-2">
              Email
            </label>
            <input
              type="email"
              name="email"
              id="email"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
              placeholder="Johnbull@example.com"
              value={this.state.formData.email}
              onChange={this.handleChange}
            />
          </div>
          <div className="pb-4">
            <label
              htmlFor="password"
              className="text-sm block text-gray-500 pb-2"
            >
              Password
            </label>
            <input
              type="password"
              name="password"
              id="password"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300"
              placeholder="Enter your password"
              value={this.state.formData.password}
              onChange={this.handleChange}
            />
          </div>

          <div className="pb-4">
            <span>
              <input
                type="checkbox"
                name="tnc"
                id="tnc"
                className=""
                checked={this.state.formData.tnc}
                onChange={this.handleChange}
              />
            </span>
            <span className="inline-block pl-3">
              <label htmlFor="tnc" className="text-sm block text-gray-500 pb-2">
                I accept all terms and conditions
              </label>
            </span>
          </div>
          <div>
            {this.props.isLoading ? (
              <PrimaryButton className="w-full bg-primary-1" type="submit">
                <Loader
                  className="flex items-center justify-center"
                  type="ThreeDots"
                  color="#FFFFFF"
                  secondaryColor="#FFFFFF"
                  height={25}
                  width={40}
                  timeout={0}
                />
              </PrimaryButton>
            ) : (
              <PrimaryButton className="w-full bg-primary-2" type="submit">
                Signup
              </PrimaryButton>
            )}
          </div>
        </form>
        <div>
          <p className=" text-gray-600">
            Already have an account?{" "}
            <Link to="/login" className="text-blue-400">
              Login Up
            </Link>
          </p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  // console.log("state = ", state);
  return {
    isLoading: state.user.isLoading,
    registerError: state.user.error ? state.user.error.message : null,
  };
};

const mapDispatchToProps = {
  register,
};

export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);
